// resourceOptimizer.js - „É™„ÇΩ„Éº„ÇπÊúÄÈÅ©Âåñ„É¶„Éº„ÉÜ„Ç£„É™„ÉÜ„Ç£

class ResourceOptimizer {
  constructor() {
    this.webpSupported = null;
    this.compressionSupported = null;
    this.initializeOptimizations();
  }

  /**
   * ÊúÄÈÅ©ÂåñÊ©üËÉΩ„ÇíÂàùÊúüÂåñ
   */
  async initializeOptimizations() {
    try {
      await this.checkWebPSupport();
      this.checkCompressionSupport();
      this.optimizeImages();
      this.optimizeCSS();
      this.optimizeJS();
      console.log('üöÄ „É™„ÇΩ„Éº„ÇπÊúÄÈÅ©Âåñ„ÅåÂàùÊúüÂåñ„Åï„Çå„Åæ„Åó„Åü');
    } catch (error) {
      console.warn(
        '‚ö†Ô∏è „É™„ÇΩ„Éº„ÇπÊúÄÈÅ©Âåñ„ÅÆÂàùÊúüÂåñ„Åß‰∏ÄÈÉ®„Ç®„É©„Éº„ÅåÁô∫Áîü„Åó„Åæ„Åó„Åü:',
        error.message
      );
      // ÈÉ®ÂàÜÁöÑ„Å™ÂàùÊúüÂåñÂ§±Êïó„ÅØË®±ÂÆπ„Åô„Çã
    }
  }

  /**
   * WebPÂØæÂøú„Çí„ÉÅ„Çß„ÉÉ„ÇØ
   * @returns {Promise<boolean>} WebPÂØæÂøúÁä∂Ê≥Å
   */
  async checkWebPSupport() {
    if (this.webpSupported !== null) {
      return this.webpSupported;
    }

    // Node.jsÁí∞Â¢É„Åß„ÅØ Image „ÅåÂà©Áî®„Åß„Åç„Å™„ÅÑ„Åü„ÇÅ„ÄÅ„Éï„Ç©„Éº„É´„Éê„ÉÉ„ÇØ
    if (typeof window === 'undefined' || typeof Image === 'undefined') {
      this.webpSupported = false;
      console.log('üì∑ WebPÂØæÂøú: Node.jsÁí∞Â¢É„ÅÆ„Åü„ÇÅÈùû„Çµ„Éù„Éº„Éà');
      return this.webpSupported;
    }

    return new Promise((resolve) => {
      const webP = new Image();
      webP.onload = webP.onerror = () => {
        this.webpSupported = webP.height === 2;
        console.log(
          `üì∑ WebPÂØæÂøú: ${this.webpSupported ? '„Çµ„Éù„Éº„Éà' : 'Èùû„Çµ„Éù„Éº„Éà'}`
        );
        resolve(this.webpSupported);
      };
      webP.src =
        'data:image/webp;base64,UklGRjoAAABXRUJQVlA4IC4AAACyAgCdASoCAAIALmk0mk0iIiIiIgBoSygABc6WWgAA/veff/0PP8bA//LwYAAA';
    });
  }

  /**
   * ÂúßÁ∏ÆÂØæÂøú„Çí„ÉÅ„Çß„ÉÉ„ÇØ
   */
  checkCompressionSupport() {
    this.compressionSupported = {
      gzip: 'gzip' in window,
      brotli: 'br' in window || navigator.userAgent.includes('Chrome'),
      deflate: 'deflate' in window,
    };
    console.log('üóúÔ∏è ÂúßÁ∏ÆÂØæÂøú:', this.compressionSupported);
  }

  /**
   * ÁîªÂÉè„ÇíÊúÄÈÅ©Âåñ
   */
  optimizeImages() {
    // Êó¢Â≠ò„ÅÆÁîªÂÉè„ÇíÊúÄÈÅ©Âåñ
    const images = document.querySelectorAll('img');
    images.forEach((img) => this.optimizeImage(img));

    // Êñ∞„Åó„ÅèËøΩÂä†„Åï„Çå„ÇãÁîªÂÉè„ÇíÁõ£Ë¶ñ
    const observer = new MutationObserver((mutations) => {
      mutations.forEach((mutation) => {
        mutation.addedNodes.forEach((node) => {
          if (node.nodeType === Node.ELEMENT_NODE) {
            if (node.tagName === 'IMG') {
              this.optimizeImage(node);
            } else {
              const images = node.querySelectorAll?.('img');
              images?.forEach((img) => this.optimizeImage(img));
            }
          }
        });
      });
    });

    observer.observe(document.body, {
      childList: true,
      subtree: true,
    });
  }

  /**
   * ÂÄãÂà•„ÅÆÁîªÂÉè„ÇíÊúÄÈÅ©Âåñ
   * @param {HTMLImageElement} img - ÁîªÂÉèË¶ÅÁ¥†
   */
  optimizeImage(img) {
    if (img.dataset.optimized) {
      return;
    }

    // WebPÂØæÂøú„ÅÆÂ†¥Âêà„ÄÅWebPÁîªÂÉè„Å´ÁΩÆÊèõ
    if (this.webpSupported && img.src && !img.src.includes('.webp')) {
      const webpSrc = this.getWebPVersion(img.src);
      if (webpSrc) {
        this.loadWebPImage(img, webpSrc);
      }
    }

    // ÈÅÖÂª∂„É≠„Éº„Éá„Ç£„É≥„Ç∞Â±ûÊÄß„ÇíËøΩÂä†
    if (!img.loading) {
      img.loading = 'lazy';
    }

    // „Éá„Ç≥„Éº„ÉâÊúÄÈÅ©Âåñ
    if ('decoding' in img) {
      img.decoding = 'async';
    }

    // „É¨„Çπ„Éù„É≥„Ç∑„ÉñÁîªÂÉè„ÅÆÊúÄÈÅ©Âåñ
    this.optimizeResponsiveImage(img);

    img.dataset.optimized = 'true';
  }

  /**
   * WebPÁâà„ÅÆURL„ÇíÂèñÂæó
   * @param {string} originalSrc - ÂÖÉ„ÅÆÁîªÂÉèURL
   * @returns {string|null} WebPÁâàURL
   */
  getWebPVersion(originalSrc) {
    // Êã°ÂºµÂ≠ê„ÇíWebP„Å´Â§âÊõ¥
    const webpSrc = originalSrc.replace(/\.(jpg|jpeg|png)$/i, '.webp');

    // Âêå„ÅòURL„ÅÆÂ†¥Âêà„ÅØnull„ÇíËøî„ÅôÔºàÊó¢„Å´WebP„Åæ„Åü„ÅØÂØæÂøú„Å™„ÅóÔºâ
    if (webpSrc === originalSrc) {
      return null;
    }

    return webpSrc;
  }

  /**
   * WebPÁîªÂÉè„ÇíË™≠„ÅøËæº„Åø
   * @param {HTMLImageElement} img - ÁîªÂÉèË¶ÅÁ¥†
   * @param {string} webpSrc - WebPÁîªÂÉèURL
   */
  async loadWebPImage(img, webpSrc) {
    try {
      // WebPÁîªÂÉè„ÅÆÂ≠òÂú®Á¢∫Ë™ç
      const response = await fetch(webpSrc, { method: 'HEAD' });
      if (response.ok) {
        const originalSrc = img.src;
        img.src = webpSrc;

        // „Éï„Ç©„Éº„É´„Éê„ÉÉ„ÇØÂá¶ÁêÜ
        img.onerror = () => {
          img.src = originalSrc;
          img.onerror = null;
        };

        console.log(`üì∑ WebPÁîªÂÉè„Å´ÊúÄÈÅ©Âåñ: ${webpSrc}`);
      }
    } catch (error) {
      // WebPÁîªÂÉè„ÅåÂ≠òÂú®„Åó„Å™„ÅÑÂ†¥Âêà„ÅØÂÖÉ„ÅÆÁîªÂÉè„Çí‰ΩøÁî®
      console.log(`üì∑ WebPÁîªÂÉè„ÅåË¶ã„Å§„Åã„Çä„Åæ„Åõ„Çì: ${webpSrc}`);
    }
  }

  /**
   * „É¨„Çπ„Éù„É≥„Ç∑„ÉñÁîªÂÉè„ÇíÊúÄÈÅ©Âåñ
   * @param {HTMLImageElement} img - ÁîªÂÉèË¶ÅÁ¥†
   */
  optimizeResponsiveImage(img) {
    if (img.srcset || img.sizes) {
      return;
    } // Êó¢„Å´Ë®≠ÂÆöÊ∏à„Åø

    const src = img.src;
    if (!src || src.startsWith('data:')) {
      return;
    }

    // „Éá„Éê„Ç§„Çπ„Éî„ÇØ„Çª„É´ÊØî„Å´Âü∫„Å•„ÅèÊúÄÈÅ©Âåñ
    const devicePixelRatio = window.devicePixelRatio || 1;
    const imgWidth = img.width || img.naturalWidth;
    const imgHeight = img.height || img.naturalHeight;

    if (imgWidth && imgHeight) {
      // È´òDPI„Éá„Ç£„Çπ„Éó„É¨„Ç§Áî®„ÅÆÁîªÂÉè„Çµ„Ç§„Ç∫„ÇíË®àÁÆó
      const optimizedWidth = Math.round(imgWidth * devicePixelRatio);
      const optimizedHeight = Math.round(imgHeight * devicePixelRatio);

      // „Çµ„Ç§„Ç∫„ÅåÂ§ß„Åç„Åô„Åé„ÇãÂ†¥Âêà„ÅØÂà∂Èôê
      const maxWidth = Math.min(optimizedWidth, 1920);
      const maxHeight = Math.min(optimizedHeight, 1080);

      // ÁîªÂÉè„Çµ„Ç§„Ç∫„ÅÆÊúÄÈÅ©ÂåñÊÉÖÂ†±„Çí„Éá„Éº„ÇøÂ±ûÊÄß„Å´‰øùÂ≠ò
      img.dataset.optimizedSize = `${maxWidth}x${maxHeight}`;
    }
  }

  /**
   * CSS„ÇíÊúÄÈÅ©Âåñ
   */
  optimizeCSS() {
    // Êú™‰ΩøÁî®CSS„ÅÆÊ§úÂá∫„Å®ÂâäÈô§
    this.removeUnusedCSS();

    // „ÇØ„É™„ÉÜ„Ç£„Ç´„É´CSS„ÅÆÁâπÂÆö
    this.identifyCriticalCSS();

    // CSSË™≠„ÅøËæº„Åø„ÅÆÊúÄÈÅ©Âåñ
    this.optimizeCSSLoading();
  }

  /**
   * Êú™‰ΩøÁî®CSS„ÇíÂâäÈô§
   */
  removeUnusedCSS() {
    const stylesheets = document.querySelectorAll(
      'link[rel="stylesheet"], style'
    );
    const usedSelectors = new Set();

    // ‰ΩøÁî®„Åï„Çå„Å¶„ÅÑ„Çã„Çª„É¨„ÇØ„Çø„Éº„ÇíÂèéÈõÜ
    stylesheets.forEach((stylesheet) => {
      try {
        const rules = stylesheet.sheet?.cssRules || stylesheet.sheet?.rules;
        if (rules) {
          Array.from(rules).forEach((rule) => {
            if (rule.selectorText) {
              const elements = document.querySelectorAll(rule.selectorText);
              if (elements.length > 0) {
                usedSelectors.add(rule.selectorText);
              }
            }
          });
        }
      } catch (error) {
        // „ÇØ„É≠„Çπ„Ç™„É™„Ç∏„É≥CSS„ÅØÁÑ°Ë¶ñ
        console.log('„ÇØ„É≠„Çπ„Ç™„É™„Ç∏„É≥CSS:', stylesheet.href);
      }
    });

    console.log(`üé® ‰ΩøÁî®‰∏≠„ÅÆCSS„Çª„É¨„ÇØ„Çø„Éº: ${usedSelectors.size}ÂÄã`);
  }

  /**
   * „ÇØ„É™„ÉÜ„Ç£„Ç´„É´CSS„ÇíÁâπÂÆö
   */
  identifyCriticalCSS() {
    const criticalElements = document.querySelectorAll(`
            header, nav, main, 
            .muscle-card, .recovery-bar, .muscle-part,
            h1, h2, h3, p, button, input
        `);

    const criticalSelectors = new Set();
    criticalElements.forEach((element) => {
      // Ë¶ÅÁ¥†„ÅÆ„ÇØ„É©„ÇπÂêç„ÇíÂèéÈõÜ
      element.classList.forEach((className) => {
        criticalSelectors.add(`.${className}`);
      });
    });

    console.log(`üé® „ÇØ„É™„ÉÜ„Ç£„Ç´„É´CSS: ${criticalSelectors.size}ÂÄã„ÅÆ„Çª„É¨„ÇØ„Çø„Éº`);

    // „ÇØ„É™„ÉÜ„Ç£„Ç´„É´CSS„ÇíLocalStorage„Å´‰øùÂ≠ò
    try {
      localStorage.setItem(
        'criticalCSS',
        JSON.stringify([...criticalSelectors])
      );
    } catch (error) {
      console.warn('„ÇØ„É™„ÉÜ„Ç£„Ç´„É´CSS‰øùÂ≠ò„Å´Â§±Êïó:', error);
    }
  }

  /**
   * CSSË™≠„ÅøËæº„Åø„ÇíÊúÄÈÅ©Âåñ
   */
  optimizeCSSLoading() {
    const stylesheets = document.querySelectorAll('link[rel="stylesheet"]');

    stylesheets.forEach((link) => {
      // Èùû„ÇØ„É™„ÉÜ„Ç£„Ç´„É´CSS„ÅØÈùûÂêåÊúüË™≠„ÅøËæº„Åø
      if (!link.href.includes('tailwind') && !link.href.includes('style.css')) {
        link.media = 'print';
        link.onload = () => {
          link.media = 'all';
        };
      }

      // „Éó„É™„É≠„Éº„Éâ„Éí„É≥„Éà„ÇíËøΩÂä†
      if (!link.as) {
        link.as = 'style';
      }
    });
  }

  /**
   * JavaScript„ÇíÊúÄÈÅ©Âåñ
   */
  optimizeJS() {
    // „É¢„Ç∏„É•„Éº„É´„ÅÆÈÅÖÂª∂Ë™≠„ÅøËæº„Åø
    this.optimizeModuleLoading();

    // ‰∏çË¶Å„Å™„Éù„É™„Éï„Ç£„É´„ÅÆÂâäÈô§
    this.removeUnnecessaryPolyfills();

    // „Éë„Éï„Ç©„Éº„Éû„É≥„ÇπÁõ£Ë¶ñ
    this.setupPerformanceMonitoring();
  }

  /**
   * „É¢„Ç∏„É•„Éº„É´Ë™≠„ÅøËæº„Åø„ÇíÊúÄÈÅ©Âåñ
   */
  optimizeModuleLoading() {
    // ÂãïÁöÑ„Ç§„É≥„Éù„Éº„Éà„ÅÆÊúÄÈÅ©Âåñ
    const originalImport = window.import;
    if (originalImport) {
      window.import = async (specifier) => {
        const startTime = performance.now();
        try {
          const module = await originalImport(specifier);
          const loadTime = performance.now() - startTime;
          console.log(
            `üì¶ „É¢„Ç∏„É•„Éº„É´Ë™≠„ÅøËæº„Åø: ${specifier} (${loadTime.toFixed(2)}ms)`
          );
          return module;
        } catch (error) {
          console.error(`üì¶ „É¢„Ç∏„É•„Éº„É´Ë™≠„ÅøËæº„ÅøÂ§±Êïó: ${specifier}`, error);
          throw error;
        }
      };
    }
  }

  /**
   * ‰∏çË¶Å„Å™„Éù„É™„Éï„Ç£„É´„ÇíÂâäÈô§
   */
  removeUnnecessaryPolyfills() {
    const modernBrowser =
      'IntersectionObserver' in window &&
      'fetch' in window &&
      'Promise' in window;

    if (modernBrowser) {
      // Âè§„ÅÑ„Éù„É™„Éï„Ç£„É´„Çπ„ÇØ„É™„Éó„Éà„ÇíÂâäÈô§
      const polyfillScripts = document.querySelectorAll(
        'script[src*="polyfill"]'
      );
      polyfillScripts.forEach((script) => {
        if (
          script.src.includes('intersection-observer') ||
          script.src.includes('fetch-polyfill')
        ) {
          script.remove();
          console.log('üóëÔ∏è ‰∏çË¶Å„Å™„Éù„É™„Éï„Ç£„É´„ÇíÂâäÈô§:', script.src);
        }
      });
    }
  }

  /**
   * „Éë„Éï„Ç©„Éº„Éû„É≥„ÇπÁõ£Ë¶ñ„ÇíË®≠ÂÆö
   */
  setupPerformanceMonitoring() {
    // Core Web Vitals „ÅÆÁõ£Ë¶ñ
    this.monitorCoreWebVitals();

    // „É™„ÇΩ„Éº„ÇπË™≠„ÅøËæº„ÅøÊôÇÈñì„ÅÆÁõ£Ë¶ñ
    this.monitorResourceTiming();

    // „É°„É¢„É™‰ΩøÁî®Èáè„ÅÆÁõ£Ë¶ñ
    this.monitorMemoryUsage();
  }

  /**
   * Core Web Vitals „ÇíÁõ£Ë¶ñ
   */
  monitorCoreWebVitals() {
    // LCP (Largest Contentful Paint)
    new PerformanceObserver((entryList) => {
      const entries = entryList.getEntries();
      const lastEntry = entries[entries.length - 1];
      console.log('üìä LCP:', lastEntry.startTime.toFixed(2), 'ms');

      // 2.5Áßí‰ª•‰∏ä„ÅÆÂ†¥Âêà„ÅØË≠¶Âëä
      if (lastEntry.startTime > 2500) {
        console.warn(
          '‚ö†Ô∏è LCP „ÅåÈÅÖ„ÅÑ„Åß„Åô:',
          lastEntry.startTime.toFixed(2),
          'ms'
        );
      }
    }).observe({ entryTypes: ['largest-contentful-paint'] });

    // FID (First Input Delay) - ‰ª£Êõø„Å®„Åó„Å¶„Ç§„Éô„É≥„Éà„É™„Çπ„Éä„Éº„ÅÆÈÅÖÂª∂„ÇíÊ∏¨ÂÆö
    let firstInputTime = null;
    // eslint-disable-next-line no-unused-vars
    const measureFID = (event) => {
      if (firstInputTime === null) {
        firstInputTime = performance.now();
        const processingTime = performance.now() - firstInputTime;
        console.log('üìä FID (Êé®ÂÆö):', processingTime.toFixed(2), 'ms');

        if (processingTime > 100) {
          console.warn('‚ö†Ô∏è FID „ÅåÈÅÖ„ÅÑ„Åß„Åô:', processingTime.toFixed(2), 'ms');
        }

        // ‰∏ÄÂ∫¶„Å†„ÅëÊ∏¨ÂÆö
        document.removeEventListener('click', measureFID);
        document.removeEventListener('keydown', measureFID);
      }
    };

    document.addEventListener('click', measureFID, { once: true });
    document.addEventListener('keydown', measureFID, { once: true });

    // CLS (Cumulative Layout Shift)
    let clsValue = 0;
    new PerformanceObserver((entryList) => {
      for (const entry of entryList.getEntries()) {
        if (!entry.hadRecentInput) {
          clsValue += entry.value;
        }
      }
      console.log('üìä CLS:', clsValue.toFixed(4));

      if (clsValue > 0.1) {
        console.warn('‚ö†Ô∏è CLS „ÅåÈ´ò„ÅÑ„Åß„Åô:', clsValue.toFixed(4));
      }
    }).observe({ entryTypes: ['layout-shift'] });
  }

  /**
   * „É™„ÇΩ„Éº„ÇπË™≠„ÅøËæº„ÅøÊôÇÈñì„ÇíÁõ£Ë¶ñ
   */
  monitorResourceTiming() {
    new PerformanceObserver((entryList) => {
      const entries = entryList.getEntries();
      entries.forEach((entry) => {
        const duration = entry.responseEnd - entry.requestStart;
        if (duration > 1000) {
          // 1Áßí‰ª•‰∏ä
          console.warn(
            '‚ö†Ô∏è ÈÅÖ„ÅÑ„É™„ÇΩ„Éº„Çπ:',
            entry.name,
            duration.toFixed(2),
            'ms'
          );
        }
      });
    }).observe({ entryTypes: ['resource'] });
  }

  /**
   * „É°„É¢„É™‰ΩøÁî®Èáè„ÇíÁõ£Ë¶ñ
   */
  monitorMemoryUsage() {
    if ('memory' in performance) {
      setInterval(() => {
        const memory = performance.memory;
        const usedMB = (memory.usedJSHeapSize / 1024 / 1024).toFixed(2);
        const totalMB = (memory.totalJSHeapSize / 1024 / 1024).toFixed(2);
        const limitMB = (memory.jsHeapSizeLimit / 1024 / 1024).toFixed(2);

        console.log(
          `üß† „É°„É¢„É™‰ΩøÁî®Èáè: ${usedMB}MB / ${totalMB}MB (Âà∂Èôê: ${limitMB}MB)`
        );

        // „É°„É¢„É™‰ΩøÁî®Èáè„Åå80%„ÇíË∂Ö„Åà„ÅüÂ†¥Âêà„ÅØË≠¶Âëä
        const usageRatio = memory.usedJSHeapSize / memory.jsHeapSizeLimit;
        if (usageRatio > 0.8) {
          console.warn(
            '‚ö†Ô∏è „É°„É¢„É™‰ΩøÁî®Èáè„ÅåÈ´ò„ÅÑ„Åß„Åô:',
            (usageRatio * 100).toFixed(1),
            '%'
          );
        }
      }, 30000); // 30ÁßíÈñìÈöî
    }
  }

  /**
   * „Éë„Éï„Ç©„Éº„Éû„É≥„ÇπÁµ±Ë®à„ÇíÂèñÂæó
   * @returns {Object} „Éë„Éï„Ç©„Éº„Éû„É≥„ÇπÁµ±Ë®à
   */
  getPerformanceStats() {
    const navigation = performance.getEntriesByType('navigation')[0];
    const paint = performance.getEntriesByType('paint');

    return {
      // „Éä„Éì„Ç≤„Éº„Ç∑„Éß„É≥ÊåáÊ®ô
      domContentLoaded:
        navigation?.domContentLoadedEventEnd -
        navigation?.domContentLoadedEventStart,
      loadComplete: navigation?.loadEventEnd - navigation?.loadEventStart,

      // „Éö„Ç§„É≥„ÉàÊåáÊ®ô
      firstPaint: paint.find((p) => p.name === 'first-paint')?.startTime,
      firstContentfulPaint: paint.find(
        (p) => p.name === 'first-contentful-paint'
      )?.startTime,

      // „É™„ÇΩ„Éº„ÇπÁµ±Ë®à
      resourceCount: performance.getEntriesByType('resource').length,

      // „É°„É¢„É™Áµ±Ë®à
      memoryUsage: performance.memory
        ? {
            used: `${(performance.memory.usedJSHeapSize / 1024 / 1024).toFixed(2)}MB`,
            total: `${(performance.memory.totalJSHeapSize / 1024 / 1024).toFixed(2)}MB`,
          }
        : null,

      // ÊúÄÈÅ©ÂåñÁä∂Ê≥Å
      webpSupported: this.webpSupported,
      compressionSupported: this.compressionSupported,
    };
  }
}

